<template>
  <view>
  <u-cell-group v-if="menuList.length>0">
	<u-cell-item v-for="(item,i) in menuList" :key="i"  :title="item.menuName" @click="linkHandle(item)" :arrow="true" arrow-direction="right"></u-cell-item>
  </u-cell-group>
  <article-list :menuId="menuId"></article-list> 
  </view>
</template>

<script>
import articleList from "../../article/article-list"
export default {
	components: {
		articleList
	},
	props:{
		menuId:{
			type:Number
		}
	},
  data() {
    return {
		menuList:[]
    };
  },
  mounted() {
    this.getMenuList()
  },
  methods: {
    // 获取接口的数据
    getList() {

    },
	linkHandle(item){
		//console.log('dddddd',item)
		uni.navigateTo({
			url: `/pages/access/access-qllgj/${item.menuCode.toLowerCase()}?id=${item.menuId}`
		})
		// //#ifdef APP-PLUS
		// plus.runtime.openURL(`192.25.105.88:3838/sample-apps/gateway/services.html`)
		// // #endif
		// //#ifdef H5
		// location.href=`http://192.25.105.88:3838/sample-apps/gateway/services.html`
		// // #endif
	},
	getMenuList(){
		this.request({
			url: "menu/list",
			data:{
				parentId:this.menuId
			},
		}).then(data => {
			if (data && data.resphead && data.resphead.succeed) {
				this.menuList = data.respbody
				console.log('datamenu',data.respbody)
				
			} else {
				uni.showToast({
					title: data.resphead.message || "获取菜单失败",
					icon: "none"
				});
			}
		});
	},

  }
};
</script>
<style lang="scss" scoped>

</style>