<template>
	<view>

		<!-- <view class="banner" v-if="listData.length>0" @click="goDetail(listData[0])">
			<image class="banner-img" style="background-color: #cccccc;" height="360rpx"
				:src="listData[0].articleCover"></image>
			<view class="banner-title">{{ listData[0].cmsName }}</view>
		</view> -->

		<view class="uni-list" v-if="listData.length>0">
			<view v-for="(item, index) in listData" :key="index">
				<view class="uni-list-cell" hover-class="uni-list-cell-hover" @click="goDetail(item)">
					<view class="uni-media-list">
						<image class="uni-media-list-logo" :src="(item.articleCover && item.articleCover.indexOf('undefined') ==-1)?item.articleCover:img"></image>
						<view class="uni-media-list-body">
							<view class="uni-media-list-text-top">{{ item.cmsName }}</view>
							<view class="uni-media-list-text-bottom">
								<!-- <text>{{ item.author }}</text> -->
								<text>{{ setTime(item.pubDate)  }}</text>
							</view>
						</view>
					</view>
				</view>
				<!-- #ifdef APP-PLUS -->
				<!-- <view class="ad-view" v-if="(index > 0 && (index+1) % 10 == 0)">
					<ad :adpid="adpid" @error="aderror"></ad>
				</view> -->
				<!-- #endif -->
			</view>
		</view>

		<view v-if="listData.length==0" style="margin: 0 20px;text-align: center;">暂无文章列表</view>
		<!-- <uni-load-more :status="status" :icon-size="8" :content-text="contentText" /> -->
	</view>
</template>

<script>
	 
	export default {
		props: {
			menuId: {
				type: Number,
			}
		},
		data() {
			return {
				img:require('static/data_none.png'),
				hasMore: false,
				banner: {},
				listData: [],
				reload: false,
				status: 'more',
				contentText: {
					contentdown: '上拉加载更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				}
			};
		},
		onLoad() {
			this.getList()
		},
		created() {

		},
		mounted() {
			this.getList()
		},
		
		updated() {
			//this.getList();
		},
		onPullDownRefresh() {
			this.reload = true;
			this.getList();
		},
		onReachBottom() {
			this.status = 'more';
			this.getList();
		},
		methods: {
			getList() {
				this.request({
					url: "/menu/cms/list",
					data: {
						menuId: this.menuId
					}
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {
						this.listData = data.respbody

					} else {
						this.isShow = false
						uni.showToast({
							title: data.resphead.message || "获取文章列表失败",
							icon: "none"
						});
					}
				});
			},
			goDetail(item) {
				if(item.cmsType == 1){
					//#ifdef APP-PLUS
					plus.runtime.openURL(item.cmsUrl)
					// #endif
					//#ifdef H5
					location.href=item.cmsUrl
					// #endif
				}else if(this.dataForm.cmsType == 2){
					uni.navigateTo({
						url: '/pages/article/article-detail?id=' + item.cmsId
					});
				}
				
				
				
			},
			setTime(val) {
				if (val) {
					return moment(val).format('yyyy-MM-DD HH:mm:SS')
				} else {
					return ''
				}

			},
			aderror(e) {
				console.log("aderror: " + JSON.stringify(e.detail));
			},
			handleToLower() {
				// if (this.hasMore) {
				//   this.params.skip += this.params.limit;
				//   this.getList();
				// } else {
				//   uni.showToast({
				//     title: "没有数据了",
				//     icon: "none"
				//   });
				// }
			}
		}
	};
</script>

<style lang="scss" scoped>
	.banner {
		height: 300rpx;
		// overflow: hidden;
		position: relative;
		background-color: #ccc;
	}

	.banner-img {
		width: 100%;
		background-color: #cccccc;
	}

	.banner-title {
		overflow: hidden;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		font-size: 32rpx;
		font-weight: 400;
		line-height: 42rpx;
		white-space: pre-wrap;
		color: white;
		z-index: 11;
		padding: 30rpx;
		background: rgba($color: #000000, $alpha: 0.5);
	}

	.uni-list {
		background-color: #FFFFFF;
		position: relative;
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	.uni-list-cell {
		position: relative;
		border-bottom: 1px solid #E3E3E5;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}

	.uni-media-list {
		padding: 22rpx 30rpx;
		box-sizing: border-box;
		display: flex;
		width: 100%;
		flex-direction: row;
	}

	.uni-media-list-logo {
		width: 180rpx;
		height: 140rpx;
		margin-right: 20rpx;
		display: inline-block;
	}

	.uni-media-list-body {
		height: auto;
		justify-content: space-around;
		display: flex;
		flex: 1;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		overflow: hidden;
	}

	.uni-media-list-text-top {
		height: 74rpx;
		font-size: 28rpx;
		overflow: hidden;
		width: 100%;
		line-height: 36rpx;
	}

	.uni-media-list-text-bottom {
		width: 100%;
		line-height: 30rpx;
		font-size: 26rpx;
		color: #8f8f94;
		display: flex;
		flex-direction: row;
		justify-content: space-between;

	}
</style>
